
@charset "utf-8";
html {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight: rgba(0, 0, 0, 0);
  -webkit-text-size-adjust: none;
  overflow-y: scroll;
  -webkit-font-smoothing: antialiased;
  font-family: '\82F9\65B9';
}
body {
  background: #f3f6f9;
}
label {
  cursor: pointer;
}
a:link,
a:visited {
  text-decoration: none;
}
input:focus {
  outline: none;
}
input,
button,
select,
textarea {
  outline: none;
}
textarea {
  resize: none;
}
input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px #fff inset;
}
textarea,
input,
select {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}
a,
abbr,
acronym,
address,
applet,
article,
aside,
audio,
b,
blockquote,
big,
body,
center,
canvas,
caption,
cite,
code,
command,
datalist,
dd,
del,
details,
dfn,
dl,
div,
dt,
em,
embed,
fieldset,
figcaption,
figure,
font,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
html,
i,
iframe,
img,
ins,
kbd,
keygen,
label,
legend,
li,
meter,
nav,
menu,
object,
ol,
output,
p,
pre,
progress,
q,
s,
samp,
section,
small,
span,
source,
strike,
strong,
sub,
sup,
table,
tbody,
tfoot,
thead,
th,
tr,
tdvideo,
tt,
u,
ul,
var {
  margin: 0;
  padding: 0;
}
article,
aside,
footer,
header,
hgroup,
nav,
section,
figure,
figcaption {
  display: block;
}
h1,
h2,
h3,
h4,
h5,
h6,
sub {
  font-size: 1rem;
}
input,
button,
select,
textarea,
sub {
  font-family: Arial, sans-serif;
}
em,
i,
b,
cite,
address,
optgroup {
  font-style: normal;
  font-weight: normal;
}
kbd,
samp,
code {
  font-family: monospace;
}
img,
input,
button,
select,
textarea {
  vertical-align: middle;
  outline: none;
  border: none;
}
ul,
ol {
  list-style: none;
}
img,
fieldset {
  border: 0;
}
abbr,
acronym {
  cursor: help;
  border-bottom: 1px dotted #000;
}
table {
  width: 100%;
  border-spacing: 0;
  border: 0;
}
table th,
table td {
  border: 0;
}
legend,
hr {
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
}
a {
  outline-style: none;
}
/**头部**/
.base_header {
  padding: 0 0.75rem;
  height: 2.2rem;
  background-color: #333333;
  position: fixed;
  top: 0;
  text-align: center;
  width: 100%;
  z-index: 99;
}
.base_header .header_box {
  padding: 0 0.875rem;
  position: relative;
}
.base_header .header_box a {
  display: inline-block;
  position: absolute;
  left: 0;
  top: 0.6rem;
  width: 0.875rem;
  height: 1rem;
}
.base_header .header_box a img {
  display: block;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.base_header .header_box span {
  display: block;
  line-height: 2.2rem;
  font-size: 0.8rem;
  color: #ffffff;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 13rem;
}
.base_header .header_box .header_right {
  position: absolute;
  right: 0;
  top: 0.05rem;
  display: inline-block;
  width: 1.5rem;
  color: #fff;
  left: inherit;
  line-height: 2.2rem;
  height: 2.2rem;
  font-size: 0.65rem;
}
.base_footer {
  position: fixed;
  bottom: 0;
  z-index: 99;
  width: 100%;
  height: 2.2rem;
  font-size: 0.55rem;
  border-top: 1px solid #bbbbbb;
  color: #707c7f;
  background-color: #ffffff;
}
.base_footer ul li dt {
  width: 1.2rem;
  height: 1.2rem;
}
.base_footer ul li dd {
  padding-top: 0.1rem;
}
.base_footer .footer_an_audition_clicked {
  background: url(/interview/static/img/icon.960d9aa.png) no-repeat;
  background-position: -0.19rem -2.7rem;
  margin-left: 2rem;
  background-size: 18rem;
}
.base_footer .footer_an_audition {
  background: url(/interview/static/img/icon.960d9aa.png) no-repeat;
  background-position: -0.2rem -1.3rem;
  margin-left: 2rem;
  background-size: 18rem;
}
.base_footer .footer_schedule_clicked {
  background: url(/interview/static/img/icon.960d9aa.png) no-repeat;
  background-position: -1.5rem -2.7rem;
  margin-left: 2rem;
  background-size: 18rem;
}
.base_footer .footer_schedule {
  background: url(/interview/static/img/icon.960d9aa.png) no-repeat;
  background-position: -1.5rem -1.3rem;
  margin-left: 2rem;
  background-size: 18rem;
}
.base_footer .footer_my_clicked {
  background: url(/interview/static/img/icon.960d9aa.png) no-repeat;
  background-position: -2.7rem -2.7rem;
  background-size: 18rem;
  margin-left: 2rem;
}
.base_footer .footer_my {
  background: url(/interview/static/img/icon.960d9aa.png) no-repeat;
  background-position: -2.7rem -1.3rem;
  background-size: 18rem;
  margin-left: 2rem;
}
.base_footer .font_clicked {
  color: #6d8ece !important;
}
.base_footer ul li {
  float: left;
  text-align: center;
  vertical-align: middle;
  width: 33.3%;
}
/**常见设置 **/
.clearfix:after,
.clearfixVisible:after {
  content: ".";
  display: block;
  height: 0;
  visibility: hidden;
  clear: both;
}
.clearfix,
.clearfixVisible {
  *zoom: 1;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
::-webkit-input-placeholder {
  color: #909da0;
}
:-moz-placeholder {
  color: #909da0;
}
::-moz-placeholder {
  color: #909da0;
}
:-ms-input-placeholder {
  color: #909da0;
}
.mui-popup-text {
  color: #707c7f !important;
}
.mui-toast-container {
  bottom: 50% !important;
}
